import React, {FC, lazy, Suspense} from "react"
import {BrowserRouter, HashRouter, Link, Route, Routes} from "react-router-dom"
import {connect, Provider} from "react-redux"
import {compose} from "redux"
import {initializeApp} from "../src/redux/app-reducer"
import store, {AppStateType} from "./redux/redux-store"

import LoginPage from "./Сomponents/Login/LoginPage";
import Preloader from "./Сomponents/commons/Preloader/Preloader"
import {UsersPage} from "./Сomponents/Users/UsersContainer";
import AppHeader from "./Сomponents/Header/Header";

import {Layout, Menu, MenuProps} from 'antd';
import {Content, Footer, Header} from "antd/es/layout/layout";
import Sider from "antd/es/layout/Sider";
import {LaptopOutlined, SettingOutlined, UserOutlined} from '@ant-design/icons';

const ProfileContainer = lazy(() => import('./Сomponents/Profile/ProfileContainer'))
const DialogsContainer = lazy(() => import("./Сomponents/Dialogs/DialogsContainer"))
const ChatPage = lazy(() => import("./Chat/ChatPage"))


type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
    initializeApp: () => void
}
export type MenuItem = Required<MenuProps>['items'][number];

export function getItem(label: React.ReactNode, key?: React.Key | null, icon?: React.ReactNode, children?: MenuItem[]): MenuItem {
    return {key, icon, children, label} as MenuItem
}
const itemsSideMenu: MenuItem[] = [
    getItem('My Profile', 'MyProfile', <UserOutlined/>, [
        getItem(
            <Link to='/profile'>Profile</Link>,
            'Profile'),
        getItem(
            <Link to='/dialogs'>Messages</Link>,
            'Messages')
    ]),
    getItem('Developers', 'Developers', <LaptopOutlined/>, [
        getItem(
            <Link to='/developers'>Developers list</Link>,
            'Developers'),
        getItem(
            <Link to='/chat'>Developers chat</Link>,
            'Developers chat')
    ]),
    getItem('Settings', 'Settings', <SettingOutlined/>, [
        getItem(
            <Link to='/news'>News</Link>,
            'News'),
        getItem(
            <Link to='/music'>Music</Link>,
            'Music')
    ])
]


class App extends React.Component<MapPropsType & DispatchPropsType> {
    catchAllUnhandledErrors = (e: PromiseRejectionEvent) => {
        alert('Some error occurred')
    }
    componentDidMount() {
        this.props.initializeApp()
        window.addEventListener("unhandledrejection", this.catchAllUnhandledErrors)
    }
    componentWillUnmount() {
        window.removeEventListener("unhandledrejection", this.catchAllUnhandledErrors)
    }

    render() {
        if (!this.props.initialized) {
            return (
                <div className='containerMy'>
                    <Preloader/>
                </div>)
        }
        return <Layout>
                <AppHeader/>
            <Layout>
                <Sider className='site-layout-background' width={200}>
                    <Menu mode='inline' style={{height: '100%', backgroundColor: 'lightgray'}} items={itemsSideMenu}/>
                </Sider>
                <Content style={{padding: '15px 0 15px 70px'}}>
                    <Suspense fallback={<Preloader/>}>
                        <Routes>
                            <Route path='/profile' element={<ProfileContainer/>}/>
                            <Route path='/profile/:userId?' element={<ProfileContainer/>}/>
                            <Route path='/dialogs/*' element={<DialogsContainer/>}/>
                            <Route path='/developers' element={<UsersPage/>}/>
                            <Route path='/chat' element={<ChatPage/>}/>
                            <Route path='/login' element={<LoginPage/>}/>
                            {/*<Route path='*' element={<div>404 NOT FOUND</div>}/>*/}
                        </Routes>
                    </Suspense>
                </Content>
            </Layout>
            <Footer style={{textAlign: 'center'}}>Social Network ©2023 Created by Dmitry Ilyin</Footer>
        </Layout>
    }
}

let mapStateToProps = (state: AppStateType) => ({
    initialized: state.app.initialized
})

let AppContainer = compose(
    //withRouter,
    connect(mapStateToProps, {initializeApp})(App))

const SocialNetworkReactJS: FC = () => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default SocialNetworkReactJS
