import {PhotosType, PostType, ProfileType} from "../types/types";
import {profileAPI} from "../api/profile-api";
import {BaseThunkType, InferActionsTypes} from "./redux-store";
import {FormAction, stopSubmit} from "redux-form";


type ActionsTypes = InferActionsTypes<typeof actions>
type InitialStateType = typeof initialState


let initialState = {
    posts: [
        {id: 1, message: "Hi, how are you?", likesCount: 5},
        {id: 2, message: "React are so cool...", likesCount: 999},
        {id: 3, message: "Hello, have a nice day!", likesCount: 234},
    ] as Array<PostType>,
    profile: null as ProfileType | null,
    status: ''
}


let profileReducer = (state = initialState, action: ActionsTypes): InitialStateType => {
    switch (action.type) {
        case 'SN/profile/ADD_POST': {
            let newPost = {
                id: 4,
                message: action.newPostText,
                likesCount: 0,
            };
            return {
                ...state,
                posts: [...state.posts, newPost]
            }
        }

        case 'SN/profile/SET_USERS_PROFILE': {
            return {
                ...state,
                profile: action.profile
            }
        }
        case 'SN/profile/SET_STATUS': {
            return {
                ...state,
                status: action.status
            }
        }
        case 'SN/profile/DELETE_POST': {
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        }
        case 'SN/profile/SAVE_PHOTO_SUCCESS': {
            return {
                ...state,
                profile: {...state.profile, photos: action.photos} as ProfileType
            }
        }
        default:
            return state
    }
}


export const actions = {
    addPostActionCreator: (newPostText: string) => ({type: 'SN/profile/ADD_POST', newPostText} as const),
    setUsersProfile: (profile: ProfileType) => ({type: 'SN/profile/SET_USERS_PROFILE', profile} as const),
    setStatus: (status: string) => ({type: 'SN/profile/SET_STATUS', status} as const),
    deletePost: (postId: number) => ({type: 'SN/profile/DELETE_POST', postId} as const),
    savePhotoSuccess: (photos: PhotosType) => ({type: 'SN/profile/SAVE_PHOTO_SUCCESS', photos} as const)
}


type ThunkType = BaseThunkType<ActionsTypes | FormAction>


export const getUsersProfile = (userId: number): ThunkType => async (dispatch) => {
    const data = await profileAPI.getProfile(userId)
    dispatch(actions.setUsersProfile(data))
}

export const getStatus = (userId: number): ThunkType => async (dispatch) => {
    const data = await profileAPI.getStatus(userId)
    dispatch(actions.setStatus(data))
}

export const updateStatus = (status: string): ThunkType => async (dispatch) => {
    const data = await profileAPI.updateStatus(status)
    if (data.resultCode === 0) {
        dispatch(actions.setStatus(status))
    }
}

export const savePhoto = (file: File): ThunkType => async (dispatch) => {
    const data = await profileAPI.savePhoto(file)
    if (data.resultCode === 0) {
        dispatch(actions.savePhotoSuccess(data.data.photos))
    }
}

export const saveProfile = (profile: ProfileType): ThunkType => async (dispatch, getState) => {
    const userId = getState().auth.userId
    const data = await profileAPI.saveProfile(profile)

    if(data.resultCode === 0) {
        if(userId !== null){
            dispatch(getUsersProfile(userId))
         } else throw new Error("userId can't be null")

    } else {
        dispatch(stopSubmit('edit-profile', {_error: data.messages[0] }))
        return Promise.reject(data.messages[0])
    }
}


export default profileReducer