import React, {FC} from 'react'
import styles from "./FormsControls.module.css"
import {Field, WrappedFieldMetaProps, WrappedFieldProps} from "redux-form"
import {FieldValidatorType} from "../../../utils/helpers/validators"


type FormControlPropsType = {
    meta: WrappedFieldMetaProps,
    children: React.ReactNode
}
const FormControl: FC<FormControlPropsType> = ({meta: {touched, error}, children}) => {
    const showError = touched && error
    return (
        <div className={styles.formControl + "" + (showError ? styles.error : "")}>
            <div>
                {children}
            </div>
            <div className={styles.errorBlockSpan}>
                {showError && <span>{error}</span>}
            </div>
        </div>
    )
}

export const Textarea: FC<WrappedFieldProps> = (props) => {
    //const {input, meta, child, ...restProps} = props
    const {input, meta, ...restProps} = props
    return <FormControl {...props}><textarea {...input} {...restProps}/></FormControl>
}
export const Input: FC<WrappedFieldProps> = (props) => {
    //const {input, meta, child, ...restProps} = props
    const {input, meta, ...restProps} = props
    return <FormControl {...props}><input {...input} {...restProps}/></FormControl>
}

export function createField<FormKeysType extends string>(placeholder: string | undefined,
                            name: FormKeysType,
                            validators: Array<FieldValidatorType>,
                            component: FC<WrappedFieldProps>,
                            props = {}, text = ''){
   return <div>
        <Field placeholder={placeholder}
               name={name}
               validate={validators}
               component={component}
               className={props}
               {...props}
        />{text}
    </div>
}

export type GetStringKeysType<T> = Extract<keyof T, string>