import React, {FC, useState} from 'react'
import s from './Paginator.module.css'
import {Button} from "antd";

type PropsType = {
    totalItemsCount: number
    pageSize: number
    currentPage: number
    onPageChanged: (pageNumber: number) => void
    portionSize?: number
}

const Paginator: FC<PropsType> = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize)

    let pagesArr = []
    for (let i = 1; i <= pagesCount; i++) {
        // @ts-ignore
        pagesArr.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionNumber = portionNumber * portionSize

    return (
        <div className={s.paginationItem}>
            {portionNumber > 1 && <Button type={"primary"} onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>PREV</Button>}
            {pagesArr
                .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
                .map(p => {
                    return <span key={p}
                                 className={currentPage === p && s.activePaginationElement}
                                 onClick={(e) => {
                                     onPageChanged(p)
                                 }}
                    >{p}</span>
                })}
            {portionCount > portionNumber && <Button type={"primary"} onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>NEXT</Button>}
        </div>
    )
}

export default Paginator