import React, {FC} from 'react';
import {NavLink} from "react-router-dom";
import {Header} from "antd/es/layout/layout";
import {useDispatch, useSelector} from "react-redux";
import {selectCurrentUserLogin, selectIsAuth} from "../../redux/auth-selectors";
import {logout} from "../../redux/auth-reducer";
import {AnyAction} from "redux";
import {Button} from "antd";


export type MapStatePropsType = {}

const AppHeader: FC<MapStatePropsType> = () => {
    const isAuthSelector = useSelector(selectIsAuth)
    const loginSelector = useSelector(selectCurrentUserLogin)

    const dispatch = useDispatch()

    const logoutCallback = () => {
        dispatch(logout() as unknown as AnyAction)
    }

    return (
        <Header>
            <div>
                {isAuthSelector
                    ? <div style={{textAlign: 'right', color: 'white'}}>
                        {loginSelector}
                        <Button style={{marginLeft: '10px'}} type={"primary"} onClick={logoutCallback}>
                            Log out</Button>
                    </div>
                    : <div style={{textAlign: 'right'}}>
                        <NavLink style={{color: 'white'}} to={'/login'}>
                            Login
                        </NavLink>
                    </div>}
            </div>
        </Header>
    )
}


export default AppHeader